import loadjs from 'loadjs';

const xhr = new XMLHttpRequest();

// helper-function for getting file paths
function getFilePaths(files) {
  const paths = [];

  if (Array.isArray(files) && files.length > 0) {
    files.forEach(function(file) {
      if (typeof file.path !== 'string') {
        console.warn('File path must be a string');
        return;
      }

      paths.push(file.path);
    });
  }

  return paths;
}

xhr.onload = () => {
  if (xhr.status >= 200 && xhr.status < 300) {
    let config = xhr.response;

    // @todo: remove overriding after creating real endpoint
    // eslint-disable-next-line global-require
    config = require('../../mocks/config.json');

    // Get paths for configured files.
    const filePaths = getFilePaths(config.files);

    loadjs([config.appFilePath, ...filePaths], {
      async: false,
      success() {
        // Create VA instance.
        const app = new window.VA(config);
        app.render();

        // Execute va() Queue.
        window.__va__.q.forEach(function(cb) {
          if (typeof cb === 'function') {
            cb(app);
          }
        });
      },
      error(depsNotFound) {
        console.error('Next dependencies not found:', depsNotFound);
      },
    });
  } else {
    console.error(xhr.responseText);
  }
};

/**
 * Loads the configuration by the specified va id.
 * Then loads and initializes the application.
 */
xhr.open(
  'GET',
  `https://jsonplaceholder.typicode.com/todos/${window.__va__.id}`
);
xhr.send();
